import { getProfiles, getFollowProfiles } from '@/api/user'
export default {
  middleware: 'authenticated',
  name: 'UserProfile',
   watchQuery: ['tab'],
  async asyncData({ params, query }) {
    const { username } = params;
    const { tab } = query;

    const tabName = tab || 'my_article'

    const { data } = await getProfiles(username);
    const { profile } = data;


    const { user } = await getFollowProfiles(username)
    console.log('user->', user)

    return { 
      profile, 
      tab:tabName 
    }
  }
}
